import React, { useState, useEffect } from "react";
import { Card, Form } from "react-bootstrap";
import ControlsMap from "../../../Properties/PropertiesConfiguration";
import TogglePropertiesJson from "../../../Properties/TogglePropertiesJson";
import CollapsibleComponent from "../../../../ReusableComponents/CollapseCard/CollapseCard";
import styles from "../../../LeftNavigation/DesignerControls/BasicControls.module.css";
import ColorPicker from "../../../../ReusableComponents/ColorPicker/ColorPicker";
import ToggleBtn from "../../../../ReusableComponents/ToggleBtn/ToggleBtn";
import SvgEditIcon from "../../../../Icons/EditIcon";
import ToggleStyle1 from "../../../../Icons/ToggleStyle1";
import ToggleStyle2 from "../../../../Icons/ToggleStyle2";
import ToggleState1 from "../../../../Icons/ToggleState1New";
import ToggleState2 from "../../../../Icons/ToggleState2New";
import RedCross from "../../../../Icons/RedCross";
import ControlLabel from "../../../../ReusableComponents/ControlLabel/ControlLabel";
import { regexValidator } from "../../../../Resources/globalFunctions";
import CustomizedAlert from "../../../../ReusableComponents/Alert/CustomizedAlert";
import { useTranslation } from "react-i18next";
import ErrorMessageComponent from "../../../../ReusableComponents/ErrorMessageComponent/ErrorMessageComponent";
import { EventListjson } from "../../../../ReusableComponents/Events/EventList";
import Events from "../../../../ReusableComponents/Events/Events";
import { FontWeightListJson } from "../../../Properties/FontWeightList";

import Dots from "../../../../Icons/ThreeDots.jsx";
import MappingFile from "../../../../ReusableComponents/Mapping/MappingFile";
import { makeStyles } from "@material-ui/core/styles";
import Tooltip from "@material-ui/core/Tooltip";
import { validateCustomControlId } from "../../RequestLogic";
import { updateUserId } from "../../Logic/UserIdLogic";
import CancelOutlinedIcon from '@material-ui/icons/CancelOutlined';
import {removeMappingFromControl} from "../../IFormDesignerLogic"
import { GetStateFromData } from "../../../CustomHooks/CustomHooks.js";

const useStyles = makeStyles({
  tooltip: {
    maxWidth: "260px",
    background: "#FFFFFF 0% 0% no-repeat padding-box",
    color: "black",
    border: "#70707075 1px solid",
    boxShadow: "0px 3px 6px #00000029",
    opacity: 1,
    fontSize: 12,
  },
  arrow: {
    "&:before": {
      border: "1px solid #70707075",
    },
    color: "white",
    fontSize: 15,
  },
});

const ToggleProperties = (props) => {
  const translationStyles1=window?.appformBuilder_configs?.selectedLanguage === "ar" || window?.appformBuilder_configs?.selectedLanguage === "ar-SA"; 
  const classes = useStyles();
  const contexts = GetStateFromData(props.containerId);
  const {
    updateJson,
    deleteControl,
    showAlertConfirmNotifications,
    fromQuickProps,
    filteredDataObjectCopy,
    setCurrentMappedDO
  } = contexts.contexts;
  const { updatedComponentInFormState } = updateJson;

  const { t } = useTranslation(["styles", "translation"]);
  const [errorMessageId, setErrorMessageId] = useState(false);
  const [alertData, setAlertData] = useState({
    open: false,
    showConfirm: "showNotification"
});
  const [mappingField, setMappingField] = useState(false);

  useEffect(()=>{
    if(fromQuickProps){
        setMappingField(true)
    }
},[fromQuickProps])

  if (props.selectedControl.style["Label"] === undefined) {
    props.selectedControl.style["Label"] = props.selectedControl.id;
  }

  function mappingOpen() {
    setMappingField(true);
  }

  function handleCombo(comboId) {
    var select = document.getElementById(comboId);
    var propertyName = select.getAttribute("name");
    var optionTag = select.options[select.selectedIndex];
    if (propertyName === "ToggleStates") {
      if (optionTag.value === "On/Off") {
        handleProperty("ToggleState1", "Off");
        handleProperty("ToggleState2", "On");
      } else if (optionTag.value === "Yes/No") {
        handleProperty("ToggleState1", "No");
        handleProperty("ToggleState2", "Yes");
      }
      handleProperty("ToggleStates", optionTag.value);
      return;
    } else if (propertyName === "LabelSwitchAlignment") {
      handleProperty(propertyName, optionTag.value);
      return;
    }
    handleProperty(propertyName, optionTag.value);
  }

  function handleToggle(e, field) {
    const value = e.target.value;
    
    if(field === "Enable") {
      props.selectedControl.style["previousEnable"] = value;
    }
    if(field === "ReadOnlyStyle") {
      props.selectedControl.style["previousReadOnly"] = value;
    }
    if(field === "ReadOnly") {
      props.selectedControl.style["previousReadOnlyValue"] = value;
    }
    props.selectedControl.style[field] = value+"";
    updatedComponentInFormState(
      props.selectedControl.id,
      props.selectedControl
    );
  }
  const updateEventData = (eventJson) => {

    var count = Object.keys(eventJson).length;
    if (count > 0) {
      var Events = {};
      Events["Events"] = eventJson;
      props.selectedControl["event"] = Events;
    }
    updatedComponentInFormState(
      props.selectedControl.id,
      props.selectedControl
    );
  };
  function handleProperty(propertyName, propertyValue, event, inputBoxId) {
    let shouldUpdate = true;
    if (propertyName === "CustomId") {
      if (propertyValue !== props.selectedControl.style.CustomId) {
        var element = event.target;
        var jsonToSend = {
          customControlId: propertyValue,
          controlid: props.selectedControl.id,
        };
        validateCustomControlId(jsonToSend)
          .then((response) => {
            if (response && response.data && response.data.status === "0") {
              let temp_style = props.selectedControl.style;
              let updatedJson = updateUserId(
                props.selectedControl, // whole control Json
                propertyValue //newly defined id
              );
              if (updatedJson) {
                updatedComponentInFormState(
                  props.selectedControl.id,
                  updatedJson
                );
              }
            } else if (
              response &&
              response.data &&
              response.data.status === "-1"
            ) {
              showAlertConfirmNotifications({
                'open':true,
                'showConfirm':'showNotification',
                'alertType':'error',
                'alertContentKey':"CustomControl Id already Exist"});


              element.value = props.selectedControl.style.CustomId || "";
            }
          })
          .catch(function (error) {
            // handle error
            element.value = props.selectedControl.style.CustomId || "";
            showAlertConfirmNotifications({
              'open':true,
              'showConfirm':'showNotification',
              'alertType':'error',
              'alertContentKey':`Server side error ${error}`});



          });
      }
      shouldUpdate = false;
    } else if (propertyName === "MaxLength") {
      propertyValue = parseInt(propertyValue);
      if (!Number.isNaN(propertyValue) && propertyValue > 0) {
        shouldUpdate = true;
      } else if (propertyValue === 0) {
        shouldUpdate = false;
      } else if (propertyValue === "") {
        shouldUpdate = true;
      } else if (Number.isNaN(propertyValue)) {
        propertyValue = "";
        shouldUpdate = true;
      }
    } else if (propertyName === "MinValue") {
      let minVal = parseInt(propertyValue);
      let maxVal = parseInt(props.selectedControl?.style?.MaxValue || "");
      if (minVal > maxVal) {
        // MIN_GREATER_THAN_MAX;
        showAlertConfirmNotifications({
          open: true,
          showConfirm: "showNotification",
          alertType: "error",
          alertContentKey: "MIN_GREATER_THAN_MAX",
        });

        window.setTimeout(() => {
          showAlertConfirmNotifications({
            open: false,
            showConfirm: "showNotification",
            alertType: "error",
            alertContentKey: "MIN_GREATER_THAN_MAX",
          });
        }, 1000);
        shouldUpdate = false;
      }
    } else if (propertyName === "MaxValue") {
      let minVal = parseInt(props.selectedControl?.style?.MinValue || "");
      let maxVal = parseInt(propertyValue);
      if (maxVal < minVal) {
        showAlertConfirmNotifications({
          open: true,
          showConfirm: "showNotification",
          alertType: "error",
          alertContentKey: "MAX_SMALLER_THAN_MIN",
        });

        window.setTimeout(() => {
          showAlertConfirmNotifications({
            open: false,
            showConfirm: "showNotification",
            alertType: "error",
            alertContentKey: "MAX_SMALLER_THAN_MIN",
          });
        }, 1000);
        shouldUpdate = false;
      }
    }
    if (shouldUpdate) {
      let temp_style = props.selectedControl.style;
      temp_style[propertyName] = propertyValue;
      props.selectedControl.style[propertyName] = propertyValue;

      updatedComponentInFormState(
        props.selectedControl.id,
        props.selectedControl
      );
    }
  }

  function deleteProperty(propertyName) {
    delete props.selectedControl.style[propertyName];
    updatedComponentInFormState(
      props.selectedControl.id,
      props.selectedControl
    );
    props.applyChanges(props.formData);
  }

  if (TogglePropertiesJson && ControlsMap) {
    var CollapsibleItems = [];
    Object.keys(TogglePropertiesJson).forEach((key, index) => {
      var collapseComponentBody = [];
      Object.keys(TogglePropertiesJson[key]).forEach((property) => {
        if (ControlsMap[property] && ControlsMap[property].type) {
          if (property === "Column1") {
            props.selectedControl.children.map((col, index) => {
              collapseComponentBody.push(
                prepareHtmlTag(
                  col.style.ColumnName,
                  ControlsMap[property].type,
                  col.id
                )
              );
            });
          } else {
            collapseComponentBody.push(
              prepareHtmlTag(property, ControlsMap[property].type)
            );
          }
        }
      });
      if (CollapsibleItems === []) {
        CollapsibleItems.push(
          <CollapsibleComponent
            id={index + ""}
            heading={key}
            body={collapseComponentBody}
          />
        );
      }

      CollapsibleItems.push(
        <CollapsibleComponent
          id={index + ""}
          heading={key}
          body={collapseComponentBody}
        />
      );
    });
  }

  function validateRegex(propertyName, propertyValue, event, inputBoxId) {
    try {
      let passValue=event?.key;
      if(propertyName==="CustomId"){
        if(event.target.selectionEnd === event.target.selectionStart){
          passValue=[propertyValue.slice(0,event.target.selectionEnd)+event.key + propertyValue.slice(event.target.selectionEnd) ].join("");
        }
        else {
          let stringArray = propertyValue.split("");
          stringArray.splice(event.target.selectionStart, event.target.selectionEnd - event.target.selectionStart);
          stringArray.splice(event.target.selectionStart, 0, ...event?.key.split(""));
          passValue = stringArray.join("");
        }
      }
      if (
        event &&
        event.which !== 8 &&
        event.which !== 46 &&
        event.code !== "Tab" &&
        !regexValidator(
          passValue,
          ControlsMap[propertyName]["regexType"]
        )
      ) {
        event.preventDefault();
        setAlertData({
          ...ControlsMap["customizedAlertNotificationData"],
          open: true,
          alertContentKey: "RegexErrorMessage",
        });
      } else {
        setAlertData({
          showConfirm: "showNotification",
          open: false,          
        });
        setErrorMessageId(false);
      }
    } catch (e) {
       console.log("Error while validating regex");
    }
  }

  function handlePropertyForLabel(propertyName, propertyValue) {
    let temp_style = props.selectedControl.style;
    temp_style[propertyName] = propertyValue;
    props.selectedControl.style[propertyName] = propertyValue;
    updatedComponentInFormState(
      props.selectedControl.id,
      props.selectedControl
    );
  }

  function prepareHtmlTag(key, type, colId) {
    switch (type.toLowerCase()) {
      case "combo":
        return (
          <>
            <div className={[styles.parentDiv2] [styles.toggleBtn]}
             style={window?.appformBuilder_configs?.selectedLanguage==="ar" || window?.appformBuilder_configs?.selectedLanguage==="ar-SA" ?{display:"flex",justifyContent:"space-between"}:null}>
              {key === "ToggleStates" ? (
                <Tooltip
                  title={t(`translation:${key} Tooltip Content`)}
                  classes={{ tooltip: classes.tooltip, arrow: classes.arrow }}
                  placement={"bottom-start"}
                  arrow
                >
                  <span>
                    {t(`translation:${ControlsMap[key].label}`)}
                  </span>
                </Tooltip>
              ) : (
                <span className={styles.leftLabel}>
                  {t(`translation:${ControlsMap[key].label}`)}
                </span>
              )}

              <div className={styles.RightLabelSelectDiv1}>
                {key !== "FontWeight" ? (
                  <>
                    <select
                      className={styles.RightLabelSelect1}
                      name={key}
                      id={props.selectedControl.id + key}
                      onChange={() =>
                        handleCombo(props.selectedControl.id + key)
                      }
                      key={props.selectedControl.id}
                    >
                      {ControlsMap[key].children.map(
                        (optionTagData, optionCount) => {
                          if (ControlsMap[key].children[optionCount].Value || ControlsMap[key].children[optionCount].Value === "" || ControlsMap[key].children[optionCount].Value === "Select") {
                            if (
                              props &&
                              props.selectedControl &&
                              props.selectedControl.style &&
                              props.selectedControl.style[key] ===
                                ControlsMap[key].children[optionCount].Value
                            ) {
                              return (
                                <option
                                  style={
                                    ControlsMap[key].children[optionCount]
                                      ?.style
                                  }
                                  value={
                                    ControlsMap[key].children[optionCount].Value
                                  }
                                  selected
                                >
                                  
                                  {t(`translation:${ControlsMap[key].children[optionCount].Title}`)}
                                </option>
                              );
                            } else {
                              return (
                                <option
                                  style={
                                    ControlsMap[key].children[optionCount]
                                      ?.style
                                  }
                                  value={
                                    ControlsMap[key].children[optionCount].Value
                                  }
                                >
                                  {t(`translation:${ControlsMap[key].children[optionCount].Title}`)}
                                </option>
                              );
                            }
                          }
                        }
                      )}
                    </select>
                  </>
                ) : (
                  <>
                    <select
                      id={props.selectedControl.id + key}
                      className={styles.RightLabelSelect1}
                      name={key}
                      onChange={() =>
                        handleCombo(props.selectedControl.id + key)
                      }
                      key={props.selectedControl.id}
                    >
                      {props.selectedControl.style &&
                      props.selectedControl.style["FontFamily"] ? (
                        <>
                          {FontWeightListJson[
                            props.selectedControl.style["FontFamily"]
                          ].children.map((optionTagData, optionCount) => {
                            if (
                              FontWeightListJson[
                                props.selectedControl.style["FontFamily"]
                              ].children[optionCount].Value ||
                              FontWeightListJson[
                                props.selectedControl.style["FontFamily"]
                              ].children[optionCount].Value === "Select" ||
                              FontWeightListJson[
                                props.selectedControl.style["FontFamily"]
                              ].children[optionCount].Value === ""
                            ) {
                              return (
                                <>
                                  {FontWeightListJson[
                                    props.selectedControl.style["FontFamily"]
                                  ].children[optionCount].Value ===
                                  props.selectedControl.style[key] ? (
                                    <option
                                      value={
                                        FontWeightListJson[
                                          props.selectedControl.style[
                                            "FontFamily"
                                          ]
                                        ].children[optionCount].Value
                                      }
                                      selected
                                    >
                                      
                                       {t(`translation:${
                                        FontWeightListJson[
                                          props.selectedControl.style[
                                            "FontFamily"
                                          ]
                                        ].children[optionCount].Title
                                      }`)}
                                    </option>
                                  ) : (
                                    <option
                                      value={
                                        FontWeightListJson[
                                          props.selectedControl.style[
                                            "FontFamily"
                                          ]
                                        ].children[optionCount].Value
                                      }
                                    >
                                      
                                      {t(`translation:${
                                        FontWeightListJson[
                                          props.selectedControl.style[
                                            "FontFamily"
                                          ]
                                        ].children[optionCount].Title
                                      }`)}
                                    </option>
                                  )}
                                </>
                              );
                            }
                          })}
                        </>
                      ) : (
                        <>
                          {FontWeightListJson["Open Sans"].children.map(
                            (optionTagData, optionCount) => {
                              if (
                                FontWeightListJson["Open Sans"].children[
                                  optionCount
                                ].Value || FontWeightListJson["Open Sans"].children[
                                  optionCount
                                ].Value === "" || FontWeightListJson["Open Sans"].children[
                                  optionCount
                                ].Value === "Select"
                              ) {
                                return (
                                  <>
                                    {FontWeightListJson["Open Sans"].children[
                                      optionCount
                                    ].Value ===
                                    props.selectedControl.style[key] ? (
                                      <option
                                        value={
                                          FontWeightListJson["Open Sans"]
                                            .children[optionCount].Value
                                        }
                                        selected
                                      >
                                        
                                        {t(`translation:${
                                          FontWeightListJson["Open Sans"]
                                            .children[optionCount].Title
                                        }`)}
                                      </option>
                                    ) : (
                                      <option
                                        value={
                                          FontWeightListJson["Open Sans"]
                                            .children[optionCount].Value
                                        }
                                      >
                                        
                                        {t(`translation:${
                                          FontWeightListJson["Open Sans"]
                                            .children[optionCount].Title
                                        }`)}
                                      </option>
                                    )}
                                  </>
                                );
                              }
                            }
                          )}
                        </>
                      )}
                    </select>
                  </>
                )}
              </div>
            </div>
            {props.selectedControl?.style?.ToggleStates === "CustomState" && key === "ToggleStates" ? (
              <>
                <div className={styles.parentDiv2}>
                  <span className={styles.leftLabel}>State1</span>{" "}
                  <ToggleState1/>
                  <input
                    aria-label={"Control Value1"} 
                    type="text"
                    defaultValue={props.selectedControl.style["ToggleState1"]}
                    className={styles.RightLabelSelect1}
                    onChange={(event) =>
                      handleProperty("ToggleState1", event.target.value)
                    }
                    key={props.selectedControl.id}
                  />
                </div>
                <div className={styles.parentDiv2}>
                  <span className={styles.leftLabel}>State2</span>{" "}
                  <ToggleState2 />
                  <input
                    aria-label={"Control Value2"}
                    type="text"
                    defaultValue={props.selectedControl.style["ToggleState2"]}
                    className={styles.RightLabelSelect1}
                    onChange={(event) =>
                      handleProperty("ToggleState2", event.target.value)
                    }
                    key={props.selectedControl.id}
                  />
                </div>
              </>
            ) : null}
          </>
        );

      case "checkbox":
        return props.selectedControl && props.selectedControl.style ? (
          <div
            className={[
              styles.textAreaParentDiv,
              styles.textAreaParentDiv1,
            ].join(" ")}
          >
            <input
              aria-label={"Control Value3"}
              id={ControlsMap[key].label}
              type="checkbox"
              defaultChecked={
                props.selectedControl.style[key] === "true" ? true : false
              }
              key={props.selectedControl.id}
              onClick={() =>
                handleProperty(
                  key,
                  props.selectedControl.style[key] === "true" ? "false" : "true"
                )
              }
            />
            <label
              aria-label={ControlsMap[key].label}
              for={ControlsMap[key].label}
              className={styles.togglePropertiesCheckboxLable}
            >
              {t(`translation:${ControlsMap[key].label}`)}
            </label>
          </div>
        ) : null;

      case "text":
        return (
          <div className={styles.parentDiv2}>
            <span className={styles.leftLabel}>
              {t(`translation:${ControlsMap[key].label}`)}
            </span>
            <div className={styles.RightLabelSelectDiv1}>
              <input
                aria-label={"Control Value4"} 
                type="text"
                defaultValue={
                  props.selectedControl && props.selectedControl.style
                    ? key === "ToggleLabel" && props?.selectedControl?.style?.[key]
                    : null
                }
                key={props.selectedControl.id}
                className={[
                  styles.RightLabelSelect1Table,
                  errorMessageId === props.selectedControl.id + key
                    ? styles.inputBoxErrorStyle
                    : null,
                ].join(" ")}
                onKeyPress={(event) =>
                  handleProperty(
                    key,
                    event.target.value,
                    event,
                    props.selectedControl.id + key
                  )
                }
                onKeyUp={(event) =>
                  handleProperty(
                    key,
                    event.target.value,
                    event,
                    props.selectedControl.id + key
                  )
                }
              />
            </div>
            {errorMessageId === props.selectedControl.id + key ? (
              <ErrorMessageComponent errorMessage="non-permissible value" />
            ) : null}
          </div>
        );

      case "colorpicker":
        return (
          <div className={"colorpicker"}>
            {t(`translation:${ControlsMap[key].label}`)}
            <ColorPicker
               key={props.selectedControl.id}
              color={props.selectedControl.style[key]}
              handleProperty={handleProperty}
              propertyName={key}
            />
          </div>
        );

      case "toggle":
        return (
          <div className={styles.toggleBtn}>
            <span> {t(`translation:${ControlsMap[key].label}`)} </span>
            <ToggleBtn
               key={props.selectedControl.id}
              handleToggle={handleToggle}
              field={key}
              id={props?.selectedControl?.style?.CustomId ? props?.selectedControl?.style?.CustomId : props?.selectedControl?.id}
              toggleState={props.selectedControl.style[key]}
            />
          </div>
        );

      case "text_top_bottom":
        return (
          <Form.Group className={styles.parentDivTopBottom}>
            <div className={styles.labelForLabel}>
                {t(`translation:${ControlsMap[key].label}`)}
            </div>

            {key === "Mapping" ? (
              <>
                <div tabIndex="0" style={{display: 'flex'}} title={props.selectedControl.style[key]}>
                  <input
                    aria-label={"Control Value5"}
                    type="text"
                    defaultValue={props.selectedControl.style[key]}
                    disabled={true}
                    key={props.selectedControl.id + props.selectedControl.style[key]}
                    id={props.selectedControl.id + key}
                    className={[styles.overflowEll,
                      styles.fullWidthTextBox,
                      errorMessageId === props.selectedControl.id + key
                        ? styles.inputBoxErrorStyle
                        : null,
                    ].join(" ")}
                    onKeyPress={(event) =>
                      validateRegex(
                        key,
                        event.target.value,
                        event,
                        props.selectedControl.id + key
                      )
                    }
                    onKeyUp={(event) =>
                      validateRegex(
                        key,
                        event.target.value,
                        event,
                        props.selectedControl.id + key
                      )
                    }
                    onBlur={(event) =>
                      handleProperty(key, event.target.value, event)
                    }
                  />
                  {props?.selectedControl?.dataclass ? <span title={t("translation: Remove Mapping")} onClick={()=>{removeMappingFromControl(props.selectedControl,updatedComponentInFormState,filteredDataObjectCopy,setCurrentMappedDO)}}
                  style={!translationStyles1?{position:"absolute",right:"15%",paddingTop:"5px", paddingRight: "5px"}:{position:"absolute",left:"15%",paddingTop:"5px", paddingLeft: "5px"}}>
                    <CancelOutlinedIcon/>
                  </span> : null}
                  <span
                    title={t("translation: View Data Objects")}
                    className={styles.mappingDotsIcon}
                    style={translationStyles1?{borderLeft:"1px solid #C4C4C4"}:null}
                    onClick={() => mappingOpen()}
                  >
                    <Dots />
                  </span>
                </div>
              </>
            ) : (
              <>
                <div>
                  <input
                    aria-label={"Control Value6"}
                    type="text"
                    id={props.selectedControl.id + key}
                    defaultValue={props.selectedControl.style[key]}
                    key={props.selectedControl.id}
                    className={styles.fullWidthTextBox}
                    onKeyPress={(event) =>
                      validateRegex(
                        key,
                        event.target.value,
                        event,
                        props.selectedControl.id + key
                      )
                    }
                    onKeyUp={(event) =>
                      validateRegex(
                        key,
                        event.target.value,
                        event,
                        props.selectedControl.id + key
                      )
                    }
                    onBlur={(event) => handleProperty(key, event.target.value, event)}
                  />
                </div>
              </>
            )}
            {errorMessageId === props.selectedControl.id + key ? (
              <ErrorMessageComponent errorMessage="non-permissible value" />
            ) : null}
          </Form.Group>
        );

        case "textbox":
          return (
            <div>
              <div className={styles.leftLabelForTextBox}>
                <div className={styles.labelForLabel} >{t(`translation:${ControlsMap[key].label}`)} </div>
                <input 
                  aria-label={"Control Value9"}
                  type="text"  
                  defaultValue={props?.selectedControl?.style?.[key]} 
                  key={
                    props.selectedControl.id +
                    props.selectedControl.style[key]
                  }
                  id={props.selectedControl.id+key}
                  className={[styles.textBoxLayoutForLable, errorMessageId === (props.selectedControl.id+key) ? styles.inputBoxErrorStyle : null].join(" ")}
                  onKeyPress={(event)=>validateRegex(key, event.target.value,event,props.selectedControl.id+key)}
                  onKeyUp={(event)=>validateRegex(key, event.target.value,event,props.selectedControl.id+key)}
                  onBlur={(event)=>handleProperty(key, event.target.value, event)}
                />
              </div>
              {errorMessageId === props.selectedControl.id + key ? (
                <ErrorMessageComponent errorMessage="non-permissible value" />
              ) : null}
            </div>
          );

      case "textarea":
        return (
          <div className={styles.textAreaParentDiv}>
            <span className={styles.leftLabel}>
              {t(`translation:${ControlsMap[key].label}`)}
            </span>
            <div>
              <textarea
                aria-label={props.selectedControl.style[key]}
                defaultValue={
                  props && props.selectedControl && props.selectedControl.style
                    ? props.selectedControl.style[key]
                    : ""
                }
                className={[
                  styles.textAreaDivToggle,
                  errorMessageId === props.selectedControl.id + key
                    ? styles.inputBoxErrorStyle
                    : null,
                ].join(" ")}

                key={props.selectedControl.id}
                onKeyPress={(event) =>
                  validateRegex(
                    key,
                    event.target.value,
                    event,
                    props.selectedControl.id + key
                  )
                }
                onKeyUp={(event) =>
                  validateRegex(
                    key,
                    event.target.value,
                    event,
                    props.selectedControl.id + key
                  )
                }
                onBlur={(event) =>
                  handlePropertyForLabel(key, event.target.value)
                }
              />
            </div>
            {errorMessageId === props.selectedControl.id + key ? (
              <ErrorMessageComponent errorMessage="non-permissible value" />
            ) : null}
          </div>
        );

      case "textareamandatorymessage":
        return (
          <>
            {props.selectedControl.style["Mandatory"] !== undefined &&
            props.selectedControl.style["Mandatory"] !== null &&
            props.selectedControl.style["Mandatory"] === "true" ? (
              <div>
                <div className={styles.errorMandatoryMessage}>
                {t(`translation:${"Error message if not filled"}`)}
                </div>
                <div className={styles.leftLabelForTextBox}>
                  <p>{t(`translation:${ControlsMap[key].label}`)} </p>
                  <textarea
                    aria-label={props.selectedControl.style[key]}
                    className={[
                      styles.textBoxLayoutStyletextAreaSpecialCharacter,
                      errorMessageId === props.selectedControl.id + key
                        ? styles.inputBoxErrorStyle
                        : null,
                    ].join(" ")}
                    defaultValue={props.selectedControl.style[key]}
                    key={props.selectedControl.id}
                    id={props.selectedControl.id + key}
                    placeholder={"Enter error message"}
                    onKeyPress={(event) =>
                      handleProperty(
                        key,
                        event.target.value,
                        event,
                        props.selectedControl.id + key
                      )
                    }
                    onKeyUp={(event) =>
                      handleProperty(
                        key,
                        event.target.value,
                        event,
                        props.selectedControl.id + key
                      )
                    }
                  ></textarea>
                </div>
                {errorMessageId === props.selectedControl.id + key ? (
                  <ErrorMessageComponent errorMessage="non-permissible value" />
                ) : null}
              </div>
            ) : null}
          </>
        );

      case "fullwidthtextinput":
        return (
          <div className={styles.textAreaParentDiv}>
            <span className={styles.leftLabel}>
              {t(`translation:${ControlsMap[key].label}`)}
            </span>
            <div className={[styles.parentDiv].join(" ")}>
              {key === "ToggleLabel" ? (
                <input
                aria-label={"Control Value10"} 
                  type="text"
                  defaultValue={
                    props &&
                    props.selectedControl &&
                    props.selectedControl.style &&
                    props.selectedControl.style[ControlsMap[key].label]
                      ? props.selectedControl.style[ControlsMap[key].label]
                      : props.selectedControl.label
                  }
                  className={[
                    styles.fullWidthInputText,
                    errorMessageId === props.selectedControl.id + key
                      ? styles.inputBoxErrorStyle
                      : null,
                  ].join(" ")}
                  onKeyPress={(event) =>
                    handleProperty(
                      key,
                      event.target.value,
                      event,
                      props.selectedControl.id + key
                    )
                  }
                  onKeyUp={(event) =>
                    handleProperty(
                      key,
                      event.target.value,
                      event,
                      props.selectedControl.id + key
                    )
                  }
                  key={props.selectedControl.id}
                />
              ) : (
                <input
                aria-label={"Control Value12"} 
                  type="text"
                  defaultValue={
                    props &&
                    props.selectedControl &&
                    props.selectedControl.style
                      ? props.selectedControl.style[ControlsMap[key].label]
                      : ""
                  }
                  className={[
                    styles.fullWidthInputText,
                    errorMessageId === props.selectedControl.id + key
                      ? styles.inputBoxErrorStyle
                      : null,
                  ].join(" ")}
                  onKeyPress={(event) =>
                    handleProperty(
                      key,
                      event.target.value,
                      event,
                      props.selectedControl.id + key
                    )
                  }
                  onKeyUp={(event) =>
                    handleProperty(
                      key,
                      event.target.value,
                      event,
                      props.selectedControl.id + key
                    )
                  }
                  key={props.selectedControl.id}
                />
              )}
              {ControlsMap[key].label === "Mapping" ? (
                <span
                  className={styles.redCrossSpan}
                  onClick={() => deleteProperty(ControlsMap[key].label)}
                >
                  {props.selectedControl &&
                  props.selectedControl.style &&
                  props.selectedControl.style[ControlsMap[key].label] ? (
                    <RedCross />
                  ) : null}
                </span>
              ) : null}
            </div>
            {errorMessageId === props.selectedControl.id + key ? (
              <ErrorMessageComponent errorMessage="non-permissible value" />
            ) : null}
          </div>
        );

      case "header":
        return (
          <p className={styles.propertiesHeaderSlider} style={window?.appformBuilder_configs?.selectedLanguage==="ar" || window?.appformBuilder_configs?.selectedLanguage==="ar-SA" ?{textAlign:"right"}:null}>
            {t(`translation:${ControlsMap[key].label}`)}
          </p>
        );

      case "hr":
        return <hr className={styles.hrTag}></hr>;

      case "fullwidthinput":
        return (
          <div className={styles.addColumnDiv}>
            <div className={styles.addColumnInputBox}>
              {key}
              <span
                id={colId}
                className={styles.addColumnInputBoxEditIcon}
                onClick={() => handleProperty()}
              >
                <SvgEditIcon />
              </span>
              <span
                id={colId}
                className={styles.addColumnInputBoxDeleteIcon}
                onClick={() => handleProperty()}
              ></span>
            </div>
          </div>
        );

      case "fullwidthinputbutton":
        return (
          <>
            <div className={styles.addColumnButtonDiv2}>
              {ControlsMap[key].children.map((obj) => {
                return (
                  <div
                    className={styles.addColumnButtonInnerDiv1}
                    onClick={() => handleProperty()}
                  >
                    {obj.Title}
                  </div>
                );
              })}
            </div>
          </>
        );

      case "stylemethods":
        return (
          <div className={styles.additionMethodsDiv2}>
            <div
              className={[
                styles.additionMethodsInnerDiv2,
                props &&
                props.selectedControl &&
                props.selectedControl.style &&
                props.selectedControl.style[ControlsMap[key].label] !==
                  "ToggleWithTickCross"
                  ? styles.toggleStyle
                  : "",
              ].join(" ")}
              onClick={() =>
                handleProperty(ControlsMap[key].label, "ToggleWithoutTickCross")
              }
            >
              <ToggleStyle1 />
            </div>
            <div
              className={[
                styles.additionMethodsInnerDiv2,
                props &&
                props.selectedControl &&
                props.selectedControl.style &&
                props.selectedControl.style[ControlsMap[key].label] ===
                  "ToggleWithTickCross"
                  ? styles.toggleStyle
                  : "",
              ].join(" ")}
              onClick={() =>
                handleProperty(ControlsMap[key].label, "ToggleWithTickCross")
              }
            >
              <ToggleStyle2 />
            </div>
          </div>
        );

      case "eventbutton":
        return (
          <Events
          key={props.selectedControl.id + "_eventsProperty"}
          json={EventListjson}
          // value={value}
          value={props.selectedControl.event}
          elemtype={props.selectedControl.elem_type}
          updateEventData={(eventJson) => updateEventData(eventJson)}
          ></Events>
        );
    }
  }

  return (
    <>
      <ControlLabel
        label="Toggle"
        handleDelete={deleteControl}
        id={props.selectedControl.id}
        customId = {props?.selectedControl?.style?.CustomId}
      />
      <div className="propertiesBody">
      <Card.Body className={styles.cardBodySheetProperties}>
        {CollapsibleItems.map((item, index) => {
          return item;
        })}
      </Card.Body>
      </div>
      {mappingField ? (
              <MappingFile
                stateOpen={true}
                setMappingField={setMappingField}
                controlData={props.selectedControl}
                elementType={props.selectedControl.elem_type}
              />
            ) : null}
      {
              alertData.open === true &&
            <CustomizedAlert alertData={alertData} />
            }
    </>
  );
};

export default ToggleProperties;
